mport './blocks/header';\n@import './blocks/form';\n@import './blocks/product';\n@import './blocks/footer';\n\n\n.active {\n  display: block;\n}","/* http://meyerweb.com/eric/tools/css/reset/ \n   v2.0 | 20110126\n   License: none (public domain)\n*/\n\nhtml, body, div, span, applet, object, iframe,\nh1, h2, h3, h4, h5, h6, p, blockquote, pre,\na, abbr, acronym, address, big, cite, code,\ndel, dfn, em, img, ins, kbd, q, s, samp,\nsmall, strike, strong, sub, sup, tt, var,\nb, u, i, center,\ndl, dt, dd, ol, ul, li,\nfieldset, form, label, legend,\ntable, caption, tbody, tfoot, thead, tr, th, td,\narticle, aside, canvas, details, embed, \nfigure, figcaption, footer, header, hgroup, \nmenu, nav, output, ruby, section, summary,\ntime, mark, audio, video {\n\tmargin: 0;\n\tpadding: 0;\n\tborder: 0;\n\tfont-size: 100%;\n\tfont: inherit;\n\tvertical-align: baseline;\n}\n/* HTML5 display-role reset for older browsers */\narticle, aside, details, figcaption, figure, \nfooter, header, hgroup, menu, nav, section {\n\tdisplay: block;\n}\nbody {\n\tline-height: 1;\n}\nol, ul {\n\tlist-style: none;\n}\nblockquote, q {\n\tquotes: none;\n}\nblockquote:before, blockquote:after,\nq:before, q:after {\n\tcontent: '';\n\tcontent: none;\n}\ntable {\n\tborder-collapse: collapse;\n\tborder-spacing: 0;\n}\n*{\n\tbox-sizing: border-box;\n}\na{\n\ttext-decoration: none;\n\tcolor: inherit;\n}\nbutton{\n\tpadding: 0;\n  border: none;\n  font: inherit;\n  color: inherit;\n  background-color: transparent;\n}",".header {\n  background-color: rgb(247, 247, 247);\n  margin-top: 80px;\n\n  @media (min-width:1140px) {\n    margin-top: 0;\n  }\n}\n\n.nav {\n  max-width: 1140px;\n  margin: 0 auto;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 14px 14px 14px 17px;\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  background-color: rgb(247, 247, 247);\n  z-index: 9;\n\n  @media (min-width: 1140px) {\n    position: relative;\n  }\n}\n\n.nav__btn {\n  display: none;\n\n  @media (min-width: 1140px) {\n    display: block;\n    @include btn;\n    @include btnAccent;\n  }\n\n}\n\n.nav__inner-item-contact {\n  display: flex;\n  gap: 5px;\n  align-items: center;\n}\n\n.nav__wrap-item {\n  display: flex;\n  gap: 43px;\n  align-items: center;\n  justify-content: space-between;\n  position: relative;\n}\n\n.navList {\n  background-color: rgb(31, 161, 129);\n}\n\n.navList__burger {\n  width: 37px;\n  height: 26px;\n  position: fixed;\n  top: 26px;\n  left: 17px;\n  cursor: pointer;\n  z-index: 10;\n\n  @media (min-width:1140px) {\n    display: none;\n  }\n}\n\n.navList__burger-el {\n  width: 37px;\n  height: 4px;\n  background-color: rgb(31, 161, 129);\n  position: absolute;\n  transition: 0.1s;\n\n  &:nth-child(1) {\n    top: 0;\n    left: 0;\n  }\n\n  &:nth-child(2) {\n    left: 0;\n    top: 50%;\n    transform: translateY(-50%);\n  }\n\n  &:nth-child(3) {\n    left: 0;\n    bottom: 0;\n  }\n}\n\n.navList__wrap-item {\n  padding: 48px 0 0 19px;\n  backdrop-filter: blur(4px);\n  background: rgba(69, 146, 127, 0.9);\n  position: fixed;\n  top: 80px;\n  width: 100%;\n  height: 100%;\n  display: none;\n  z-index: 9999;\n\n  @media (min-width:1140px) {\n\n    display: flex;\n    align-items: center;\n    justify-content: left;\n    background-color: inherit;\n    width: 1140px;\n    height: 43px;\n    position: relative;\n    top: 0;\n    left: 0;\n    margin: 0 auto;\n    padding: 10px 14px;\n  }\n\n}\n\n.navList__inner-list {\n  margin-bottom: 39px;\n\n  @media (min-width:1140px) {\n    display: flex;\n    max-width: 1140px;\n    gap: 20px;\n    align-items: center;\n    margin-bottom: 0;\n\n  }\n}\n\n.navList__list {\n  margin-bottom: 10px;\n\n  @media (min-width:1140px) {\n    margin-bottom: 0;\n\n  }\n}\n\n.navList__list-link {\n  @include text-m;\n  color: rgb(255, 255, 255);\n  font-size: 16px;\n}\n\n.navList__list-btn {\n  @include btn;\n  background-color: rgb(246, 246, 246);\n  color: rgb(31, 161, 129);\n  font-weight